body{
  font-family: 'Open Sans', sans-serif;
}
.margin-left-auto{
  margin-left: auto !important;
}

h1{
  text-transform: uppercase;
  font-size: 3em;
  font-weight: 300;
}

.text-center{
  text-align: center;
}

.top-nav{
    display:-webkit-flex;
    display:flex;
    background-color: #003057;
  }
  .nav-item{
      color: #FFF;
      margin: 2rem;
      text-decoration: none;
      position: relative;
  }

  .nav-item.active:before{
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    bottom: 0;
    left: 0;
    background-color: #FFF;
  }

  .nav-item:not(.active):before{
      content: "";
      position: absolute;
      width: 100%;
      height: 3px;
      bottom: 0;
      left: 0;
      background-color: #FFF;
      visibility: hidden;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      transition: all 0.1s ease-in-out 0s;
  }

  .nav-item:hover:before{
      visibility: visible;
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
  }
